body {
  position: relative;
  min-width: 320px;
}
*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
}
html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.content-box-component {
  -webkit-box-sizing: content-box;
  box-sizing: content-box;
}
a {
  text-decoration: none;
  transition: all 0.1s ease;
}
a:active {
  opacity: 0.5;
}
img {
  max-width: 100%;
  height: auto;
}
.arcticmodal-overlay,
.arcticmodal-container {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 1000;
}
.arcticmodal-container {
  overflow: auto;
  margin: 0;
  padding: 0;
  border: 0;
  border-collapse: collapse;
}
*:first-child + html .arcticmodal-container {
  height: 100%;
}
.arcticmodal-container_i {
  height: 100%;
  margin: 0 auto;
}
.arcticmodal-container_i2 {
  padding: 24px;
  margin: 0;
  border: 0;
  vertical-align: middle;
}
.arcticmodal-error {
  padding: 20px;
  border-radius: 10px;
  background: #000;
  color: #fff;
}
.arcticmodal-loading {
  width: 80px;
  height: 80px;
  border-radius: 10px;
  background: #000 url(loading.gif) no-repeat 50% 50%;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  max-width: 1310px;
  padding: 0 10px;
  margin: 0 auto;
}
@media (max-width: 1024px) {
  .container {
    padding: 0 15px;
  }
}
.button__icon {
  position: relative;
  margin-left: 15px;
  fill: transparent;
  stroke: #ffffff;
  transition: all 0.2s ease;
}
.button {
  display: flex;
  align-items: center;
  padding: 10px 20px;
  justify-content: center;
  min-height: 53px;
  font-weight: 600;
  font-size: 16px;
  line-height: 22px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #FAFAFA;
  border: none;
  border-bottom: 3px solid #ffffff;
  background-color: #00A5DD;
  outline: none;
  cursor: pointer;
  transition: all 0.1s ease;
}
.button:hover,
.button:focus {
  color: #00A5DD;
  background-color: #ffffff;
}
.button:hover .button__icon,
.button:focus .button__icon {
  right: -5px;
  stroke: #00A5DD;
}
.button:active {
  opacity: 0.5;
}
.button--white {
  color: #00a5dd;
  background-color: #ffffff;
  border: 1px solid #00a5dd;
  border-bottom: 3px solid #00a5dd;
}
.button--white .button__icon {
  stroke: #00a5dd;
}
.button--white:hover,
.button--white:focus {
  color: #ffffff;
  background-color: #00A5DD;
}
.button--white:hover .button__icon,
.button--white:focus .button__icon {
  stroke: #ffffff;
}
.main {
  padding-top: 42px;
  background-color: #FAFAFA;
}
.main--home-page {
  padding-top: 10px;
}
@media (max-width: 1024px) {
  .main {
    padding-top: 15px;
  }
  .main--home-page {
    padding-top: 0;
  }
}
.subscribe form {
  display: flex;
  flex-direction: column;
  margin-bottom: 16px;
}
.subscribe label {
  position: relative;
  display: flex;
  flex-direction: column-reverse;
  width: 100%;
  margin-bottom: 6px;
  padding-top: 18px;
  border-bottom: 1px solid #ffffff;
}
.subscribe label span {
  position: absolute;
  top: 18px;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.02em;
  color: #FAFAFA;
  transition: all 0.2s ease;
}
.subscribe input:not([value=""]) + span {
  opacity: 0;
}
.subscribe input {
  display: block;
  padding-bottom: 3px;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.02em;
  color: #FAFAFA;
  background: transparent;
  border: none;
  border-bottom: 2px solid transparent;
  outline: none;
  transition: all 0.2s ease;
}
.subscribe input:focus {
  border-color: #FAFAFA;
}
.subscribe input:focus + span {
  top: 0;
  font-size: 11px;
  opacity: 1;
}
.subscribe button,
.subscribe input[type=submit] {
  width: 100%;
  margin-top: 22px;
  display: flex;
  align-items: center;
  padding: 14px 22px;
  justify-content: center;
  min-height: 53px;
  font-weight: 600;
  font-size: 15px;
  line-height: 24px;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  color: #FAFAFA;
  border: 1px solid #ffffff;
  border-bottom: 3px solid #ffffff;
  background-color: transparent;
  cursor: pointer;
  transition: all 0.1s ease;
}
.subscribe button:hover,
.subscribe input[type=submit]:hover,
.subscribe button:focus,
.subscribe input[type=submit]:focus {
  color: #00A5DD;
  background-color: #ffffff;
}
.subscribe button:active,
.subscribe input[type=submit]:active {
  opacity: 0.5;
}
.subscribe .ml-form-embedSubmitLoad {
  display: inline-block;
  width: 20px;
  height: 20px;
}
.subscribe .ml-form-embedSubmitLoad:after {
  content: " ";
  display: block;
  width: 11px;
  height: 11px;
  margin: 1px;
  border-radius: 50%;
  border: 4px solid #fff;
  border-color: #fff #fff #fff transparent;
  animation: ml-form-embedSubmitLoad 1.2s linear infinite;
}
@keyframes ml-form-embedSubmitLoad {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}
.subscribe__title {
  margin: 0;
  margin-bottom: 30px;
  margin-left: -10px;
  margin-right: -10px;
  font-family: "Marmelad", "Arial", sans-serif;
  font-size: 24px;
  line-height: 29px;
  font-weight: 400;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #FAFAFA;
}
.subscribe__policy {
  font-weight: 600;
  font-size: 11px;
  line-height: 17px;
  letter-spacing: 0.02em;
  color: #FAFAFA;
}
.subscribe__policy .privacy-policy-link {
  color: #FFCED4;
  text-decoration: underline;
  cursor: pointer;
  transition: all 0.1s ease;
}
.subscribe__success {
  color: #ffffff;
  text-align: center;
  font-size: 20px;
}
@media (max-width: 1024px) {
  .subscribe label {
    margin-bottom: 7px;
  }
  .subscribe label span {
    top: 20px;
    font-weight: 400;
  }
  .subscribe input:focus + span {
    top: 3px;
  }
  .subscribe input[type=submit] {
    margin-top: 24px;
    padding: 8px 22px;
    min-height: 43px;
    font-size: 13px;
    line-height: 20px;
    letter-spacing: 0.02em;
  }
  .subscribe__policy {
    font-size: 10px;
    line-height: 15px;
    letter-spacing: 0;
  }
}
.breadcrumbs {
  margin: 0;
  padding: 0;
  padding-left: 4px;
  display: flex;
  list-style: none;
}
.breadcrumbs__link {
  position: relative;
  padding-right: 17px;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.02em;
  color: #9E9E9E;
}
.breadcrumbs__link::after {
  content: '';
  position: absolute;
  right: 0;
  top: -2px;
  color: #00a5dd;
  font-size: 11px;
  font-weight: 700;
}
a.breadcrumbs__link {
  cursor: pointer;
  transition: all 0.1s ease;
}
a.breadcrumbs__link:hover,
a.breadcrumbs__link:focus {
  color: #00A5DD;
}
.breadcrumbs__item {
  margin-right: 8px;
}
.breadcrumbs__item:last-child {
  margin-right: 0;
}
.breadcrumbs__item:last-child::after {
  display: none;
}
.breadcrumbs__item:last-child .breadcrumbs__link {
  color: #C4C4C4;
}
.breadcrumbs__item:last-child .breadcrumbs__link::after {
  display: none;
}
.breadcrumbs__item:last-child a::after {
  display: none;
}
.content {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding-bottom: 40px;
}
.content__main {
  width: 100%;
  max-width: 847px;
  margin-right: 10px;
}
.content__main--home-page {
  padding-top: 40px;
}
.content__main--page {
  max-width: 876px;
}
.content__breadcrumbs {
  margin-bottom: 23px;
}
.content__sidebar {
  width: 370px;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 41px 10px 10px 10px;
  margin-right: 12px;
  background-color: #ffffff;
}
.content__sidebar-header {
  margin: 0;
  font-weight: 600;
  font-size: 18px;
  line-height: 25px;
  letter-spacing: 0.02em;
  color: #323232;
}
@media (max-width: 1024px) {
  .content {
    flex-direction: column;
    padding: 0;
  }
  .content__main {
    max-width: 100%;
    margin-right: 0;
    margin-bottom: 10px;
  }
  .content__main--home-page {
    padding-top: 0;
  }
  .content__breadcrumbs {
    flex-wrap: wrap;
    padding: 0 15px;
    padding-left: 18px;
    margin-bottom: 43px;
  }
  .content__sidebar {
    width: 100%;
    align-items: center;
    padding: 0 15px;
    padding-top: 35px;
    margin-bottom: 90px;
  }
  .content__sidebar-item {
    width: 100% !important;
    max-width: 100% !important;
  }
}
.nav-links {
  display: flex;
}
.nav-links .page-numbers {
  min-width: 34px;
  height: 34px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 8px;
  font-weight: bold;
  font-size: 20px;
  line-height: 27px;
  text-transform: uppercase;
  color: #696969;
  border: 1px solid #E5E5E5;
  fill: transparent;
  stroke: #696969;
}
.nav-links a.page-numbers {
  cursor: pointer;
  transition: all 0.1s ease;
}
.nav-links a.page-numbers:hover,
.nav-links a.page-numbers:focus {
  color: #00A5DD;
  border-color: #00A5DD;
  stroke: #00A5DD;
}
.nav-links a.page-numbers:active {
  opacity: 0.5;
}
.nav-links .dots {
  border: none;
}
.nav-links .current {
  color: #00A5DD;
  border-color: #00A5DD;
}
.nav-links .next,
.nav-links .prev {
  border: none;
}
.nav-links .prev {
  margin-right: 0;
  margin-left: 10px;
  transform: rotate(-180deg);
}
.burger {
  width: 40px;
  height: 40px;
  padding: 0;
  border: none;
  background-color: transparent;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url("../img/burger.svg");
}
.up {
  visibility: hidden;
  position: fixed;
  z-index: 1000;
  bottom: 30px;
  right: 30px;
  display: inline-block;
  width: 50px;
  height: 50px;
  border-radius: 4px;
  opacity: 0;
  background-color: #00a5dd;
  background-size: 60%;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url("../img/up.svg");
  transition: background-color 0.3s, opacity 0.5s, visibility 0.5s;
  outline: none;
}
.up:hover {
  cursor: pointer;
  background-color: #333;
}
.up:active {
  background-color: #555;
}
.up.show {
  visibility: visible;
  opacity: 1;
}
.menu {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
}
.menu a {
  display: block;
  padding: 10px 35px 10px 34px;
  font-size: 16px;
  line-height: 22px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #FAFAFA;
  border-right: 1px solid #EEEEEE;
  cursor: pointer;
  transition: all 0.1s ease;
}
.menu a:hover,
.menu a:focus {
  color: #00A5DD;
}
.menu a:active {
  opacity: 0.5;
}
.menu li:first-child a {
  padding-left: 12px;
}
.menu li:last-child a {
  border: none;
}
.menu--bottom {
  flex-wrap: wrap;
}
.menu--bottom li:last-child a {
  padding-right: 20px;
}
.menu--bottom a {
  color: #323232;
  white-space: nowrap;
  border-color: #B4B4B4;
}
@media (max-width: 1024px) {
  .menu--bottom {
    width: 100%;
  }
  .menu--bottom li:first-child a {
    padding-left: 0;
  }
  .menu--bottom li:last-child a {
    padding-right: 0;
  }
  .menu--bottom a {
    padding: 5px 0;
    text-align: center;
    border: none;
  }
}
.header {
  padding-top: 5px;
  background-color: #EEEEEE;
}
.header__top {
  display: flex;
  justify-content: space-between;
  margin-bottom: -35px;
}
.header__left {
  padding-top: 5px;
}
.header__city {
  margin-right: 95px;
}
.header__controls {
  margin-bottom: 9px;
  padding-left: 7px;
}
.header__logo {
  display: block;
}
a.header__logo {
  transition: all 0.1s ease;
  cursor: pointer;
}
a.header__logo:hover,
a.header__logo:focus {
  opacity: 0.8;
}
a.header__logo:active {
  opacity: 0.5;
}
.header__label {
  display: flex;
  align-items: center;
  margin: 0;
  margin-bottom: 8px;
  font-size: 18px;
  line-height: 25px;
  letter-spacing: 0.052em;
  text-transform: uppercase;
  color: #332233;
}
.header__label--small {
  font-size: 14px;
  line-height: 19px;
  letter-spacing: 0.03em;
}
.header__label:last-child {
  margin-bottom: 0;
}
.header__label-dot {
  margin: 0 13px;
  width: 6px;
  height: 6px;
  background: #00A5DD;
}
.header__bottom {
  padding: 4px 0;
  background: #4A4A4A;
  box-shadow: 0 5px 7px rgba(0, 0, 0, 0.1);
}
.header__bottom-container {
  display: flex;
  align-items: center;
}
.header__callback {
  margin-left: auto;
